<template>
  <div class="select-overspread repair-bg auto" >
    <validator name="v" >
      <div class="auto repair-info-content compatible" >
        <div class="row auto">
          <div class="col-xs-12 col-sm-6 col-md-6 form-input-group mg8" :class="[$v.f_constructionunit_name.required ? 'has-error': '']">
            <label class="text-justify lb-left" for="f_constructionunit_name">施工部门签字人：</label>
            <input type="text" class="form-control" id="f_constructionunit_name"  v-model="model.f_constructionunit_name" v-validate:f_constructionunit_name='{required: true }'>
          </div>
          <div class="col-xs-12 col-sm-6 col-md-6 form-input-group" :class="[$v.f_constructionunit_date.required ? 'has-error': '']">
            <label class="text-justify lb-left" for="costmatter">施工部门签字日期：</label>
            <datepicker
              v-validate:f_constructionunit_name='{required: true }'
              :value.sync="model.f_constructionunit_date"
              :disabled-days-of-Week="[]"
              :format="'yyyy-MM-dd HH:mm:ss'"
              v-model="model.f_constructionunit_date"
              :show-reset-button="reset" placeholder="请输入签字日期" style="margin-bottom: 8px">
            </datepicker>
          </div>
          <div class="col-xs-12 col-sm-6 col-md-6 form-input-group" :class="[$v.f_install_name.required ? 'has-error': '']">
            <label class="text-justify lb-left" >安装人员：</label>
            <input type="text" class="form-control" v-model="model.f_install_name" v-validate:f_install_name='{required: true }' />
          </div>
          <div class="col-xs-12 col-sm-6 col-md-6 form-input-group" :class="[$v.f_designunit_name.required ? 'has-error': '']">
            <label class="text-justify lb-left" >设计部门签字人：</label>
            <input type="text" class="form-control"  v-model="model.f_designunit_name"  v-validate:f_designunit_name='{required: true }' />
          </div>
          <div class="col-xs-12 col-sm-6 col-md-6 form-input-group" :class="[$v.dunitdate.required ? 'has-error' : '' ]">
            <label class="text-justify lb-left">设计部门签字日期：</label>
            <datepicker
              :value.sync="model.f_designunit_date"
              :disabled-days-of-Week="[]"
              :format="'yyyy-MM-dd HH:mm:ss'"
              v-model="model.f_designunit_date"
              :show-reset-button="reset" placeholder="请输入设计单位签字日期" style="margin-bottom: 8px">
            </datepicker>
          </div>
          <div class="col-xs-12 col-sm-12 col-md-6 form-input-group">
            <input type="checkbox" class="" id="f_check_indoorproject" v-model="model.f_check_indoorproject"
                   v-validate:f_check_indoorproject='{required: true }'/>
            <label for="f_check_indoorproject" class="text-justify lb-left">户内工程是否验收通过</label>
          </div>
          <div class="col-xs-12 col-sm-6 col-md-6 form-input-group" :class="[$v.f_buildunit_name.required ? 'has-error': '']">
            <label class="text-justify lb-left" >建设部门签字人：</label>
            <input type="text" class="form-control"  v-model="model.f_buildunit_name"  v-validate:f_buildunit_name='{required: true }' />
          </div>
          <div class="col-xs-12 col-sm-6 col-md-6 form-input-group">
            <label class="text-justify lb-left">建设部门签字日期：</label>
            <datepicker
              :value.sync="model.f_buildunit_date"
              :disabled-days-of-Week="[]"
              :format="'yyyy-MM-dd HH:mm:ss'"
              v-model="model.f_buildunit_date"
              :show-reset-button="reset" placeholder="请输入建设单位签字日期" style="margin-bottom: 8px">
            </datepicker>
          </div>
          <div class="col-xs-12 col-sm-12 col-md-6 form-input-group">
            <input type="checkbox" class="" id="f_check_outdoorsproject" v-model="model.f_check_outdoorsproject"
                   v-validate:f_check_outdoorsproject='{required: true }'/>
            <label for="f_check_outdoorsproject" class="text-justify lb-left">庭院工程是否验收通过</label>
          </div>
          <div class="col-xs-12 col-sm-6 col-md-6 form-input-group" :class="[$v.f_supervisionunit_name.required ? 'has-error': '']">
            <label class="text-justify lb-left" >监理部门签字人：</label>
            <input type="text" class="form-control"  v-model="model.f_supervisionunit_name"  v-validate:f_supervisionunit_name='{required: true }' />
          </div>
          <div class="col-xs-12 col-sm-6 col-md-6 form-input-group" :class="[$v.dunitdate.required ? 'has-error' : '' ]">
            <label class="text-justify lb-left">监理部门签字日期：</label>
            <datepicker
              :value.sync="model.f_upervisionunit_date"
              :disabled-days-of-Week="[]"
              :format="'yyyy-MM-dd HH:mm:ss'"
              v-model="model.f_upervisionunit_date"
              :show-reset-button="reset" placeholder="请输入监理单位签字日期" style="margin-bottom: 8px">
            </datepicker>
          </div>
          <div class="col-xs-12 col-sm-6 col-md-6 form-input-group" :class="[$v.dunitdate.required ? 'has-error' : '' ]">
          <label class="text-justify lb-left">实际竣工日期：</label>
          <datepicker
            :value.sync="model.f_project_enddate"
            :disabled-days-of-Week="[]"
            :format="'yyyy-MM-dd HH:mm:ss'"
            v-model="model.f_project_enddate"
            :show-reset-button="reset" placeholder="请输入实际竣工日期" style="margin-bottom: 8px">
          </datepicker>
        </div>
          <div class="col-xs-12 col-sm-6 col-md-6 form-input-group">
            <label class="text-justify lb-left">验收备注:</label>
            <textarea class="form-control" cols="30" rows="4" style="resize: vertical;" v-model="model.f_completed_remarks" ></textarea>
          </div>
          <div class="col-xs-12 col-sm-6 col-md-6 form-input-group">
            <label class="text-justify lb-left" for="costmatter">部门:</label>
            <input type="text" class="form-control" id="costmatter" v-model="model.f_completed_department" readonly/>
          </div>
          <div class="col-xs-12 col-sm-6 col-md-6 form-input-group">
            <label class="text-justify lb-left" for="costmatter">操作人:</label>
            <input type="text" class="form-control" id="costmatter" v-model="model.f_completed_operator" readonly/>
          </div>
        </div>
        <div class="col-xs-12 col-sm-6 col-md-6 form-input-group">
          <label class="lb-left text-justify">分公司:</label>
          <input type="text" class="form-control"  v-model="model.f_filiale" readonly/>
        </div>
        <div class="col-xs-12 col-sm-6 col-md-6 form-input-group">
          <label class="lb-left text-justify">操作日期:</label>
          <input type="text" class="form-control"  v-model="model.f_completed_date"  readonly/>
        </div>
      </div>
      <div style="text-align:center;margin-top: 5px;" v-if="model.canedit">
        <button @click="save" type="button" class="btn btn-primary radius_btn" >保存</button>
        <button @click="upload" type="button" class="btn btn-primary radius_btn" >上传</button>
      </div>
    </validator>
  </div>
</template>
<script>
  import Vue from 'vue'
  import * as Util from '../Util'
  import co from 'co'

  let uploadApply = function * (self) {
    // self.model.f_app_upload = '已上传'
    var param = {
      model: {
        f_processid: self.model.f_process_id
      },
      loginUser: {
        id: Vue.user.id,
        name: Vue.user.name,
        ename: Vue.user.ename
      },
      employeetemplate: self.employeetemplate,// 指派勘察人员
      subdep: '提交'
    }
    var data ={tablename:'t_apply',parameters:self.model,param: param}
    // var url = self.$androidUtil.getProxyUrl() + '/rs/logic/uploadapply'
    // console.log(`即将上传，url>>>> ${url}`)
    // let res = yield self.$resetpost(url, {data:data})
    let res = yield self.$androidUtil.bzLogic('uploadapply', data)
    console.log(`上传返回json>>>>>>>>>>>> ${JSON.stringify(res)}`)
    if(!res){
      self.$showMessage('上传失败！')
      return
    }
    if(res.code != 200){
      self.$showMessage('上传失败，原因:' + res.msg)
      return
    }
    self.$showMessage('上传成功！')
    self.updateState()
    self.$parent.$back()
  }

  export default {
    title: '竣工验收',
    props: {
      model: { // 传入一条报建信息
        type: Object,
        f_completed_date: '',
        f_completed_department: '',
        f_completed_operator: ''
      }
    },
    data () {
      return {
        signType:{
          type: Boolean,
          default: true
        },
      }
    },
    methods: {
      validPageData(){
        if(!this.model.f_constructionunit_name || '' == this.model.f_constructionunit_name.trim()){
          this.$showMessage('施工部门签字人不能为空')
          return false
        }
        if(!this.model.f_constructionunit_date || '' == this.model.f_constructionunit_date.trim()){
          this.$showMessage('施工部门签字日期不能为空')
          return false
        }
        if(!this.model.f_install_name || '' == this.model.f_install_name.trim()){
          this.$showMessage('安装人员不能为空')
          return false
        }
        if(!this.model.f_designunit_name || '' == this.model.f_designunit_name.trim()){
          this.$showMessage('设计部门签字人不能为空')
          return false
        }
        if(!this.model.f_designunit_date || '' == this.model.f_designunit_date.trim()){
          this.$showMessage('设计部门签字日期不能为空')
          return false
        }
        if(!this.model.f_buildunit_name || '' == this.model.f_buildunit_name.trim()){
          this.$showMessage('建设部门签字人不能为空')
          return false
        }
        if(!this.model.f_buildunit_date || '' == this.model.f_buildunit_date.trim()){
          this.$showMessage('建设部门签字日期不能为空')
          return false
        }
        if(!this.model.f_supervisionunit_name || '' == this.model.f_supervisionunit_name.trim()){
          this.$showMessage('监理部门签字人不能为空')
          return false
        }
        if(!this.model.f_upervisionunit_date || '' == this.model.f_upervisionunit_date.trim()){
          this.$showMessage('监理部门签字日期不能为空')
          return false
        }
        if(!this.model.f_project_enddate || '' == this.model.f_project_enddate.trim()){
          this.$showMessage('实际竣工日期不能为空')
          return false
        }
        return true
      },
      upload() {
        if(!this.validPageData()){
          return
        }
        // this.model.f_gas_type = this.model.f_gas_type[0] ? this.model.f_gas_type[0] : ''
        this.model.f_app_upload = '已完成'
        console.log(`前台开始本地保存，传入数据json>>>>>>>${JSON.stringify(this.model)}`)
        let res = this.$androidUtil.bzLogic('saveapplyforapp', {parameters:this.model})
        if(res.code != 200){
          this.$showMessage('保存失败,上传终止！')
          return
        }
        let gen = uploadApply(this)
        co(gen)
      },
      mute () {
        HostApp.mute()
      },
      // 上传完成后回写本地上传状态
      updateState(){
        console.log(`前台上传完成，开始回写本地上传状态`)
        this.model.f_app_upload = '已上传'
        this.$androidUtil.bzLogic('saveapplyforapp', {parameters:this.model})
      },
      save() {
        if(!this.validPageData()){
          return
        }
        this.model.f_app_upload = '已完成'
        console.log(`前台开始本地保存，传入数据json>>>>>>>${JSON.stringify(this.model)}`)
        let res = this.$androidUtil.bzLogic('saveapplyforapp', {parameters:this.model})
        console.log(`前台保存结束，返回res>>>>>>>${JSON.stringify(res)}`)
        if(res.code == 200){
          console.log(`前台保存成功!`)
          this.$showMessage('保存成功！')
        }else{
          console.log('前台保存失败')
          this.$showMessage('保存失败！')
        }
      }
    },
    ready(){
      if(this.model && this.model.f_completed_date){
        this.model.f_completed_date = Util.toStandardTimeString()
      }
      if(navigator.userAgent.match('iPad') || navigator.userAgent.match('iPhone')){
        this.signType = false
      }
      if(this.model && this.model.f_single_man){
        this.model.f_single_man = this.f_single_man
      }
      if(Vue.user){
        console.log('获取到Vue.user')
        this.model.f_completed_department = Vue.user.f_parentname
        this.model.f_completed_operator = Vue.user.name
      }
    },
    watch:{
      'model.f_check_outdoorsproject'(val){
        // this.$showMessage(val)
        if(val){
          this.model.f_check_outdoorsproject = 1
        }else{
          this.model.f_check_outdoorsproject = 0
        }
      },
      'model.f_check_indoorproject'(val){
        if(val){
          this.model.f_check_indoorproject = 1
        }else{
          this.model.f_check_indoorproject = 0
        }
      }
    }
  }
</script>
<style>
  .radius_btn{
    border-radius:  5px;
    margin-top: 0.1rem;
  }

</style>



